.home-title{
    font-size: xx-large;
    color: var(--text-color);
}

.outlined-text {
    font-family: 'YourFontFamily', sans-serif; /* Use the desired font family */
    font-size: 3rem; /* Adjust based on your design */
    font-weight: 700; /* Adjust weight to match the design */
    color: transparent; /* Make the text itself transparent */
    -webkit-text-stroke: 1px var(--text-color); /* The color and thickness of the outline */
    text-transform: uppercase; /* Optional: To transform the text to uppercase */
    letter-spacing: 2px; /* Adjust spacing to match the design */
  }

.outlined-text:hover{
    .fancy-letter{
       transform:scale(1.3);
    }
}

  .fancy-letter {
    font-size: 4rem; /* Increase the size of the letters */
    font-weight: 900; /* Make them bolder */
    transform: scale(1.1); /* Slightly increase both width and height */
    display: inline-block; /* Ensure transform affects only the letter */
    -webkit-text-stroke: 1px var(--text-color); /* Optional: Keep the outline or change it */
    letter-spacing: 2px; /* Add some spacing between letters */
    text-shadow: 2px 2px 6px var(--text-color); /* Add a more prominent shadow */
    font-style: italic; /* Add an italic style for fanciness */
    transition:ease ;
    transition-property: transform;
    transition-duration: 1s;
  }

  .exclaimation {
    display: inline-block;
    width: 3ch; /* Adjust the width to fit the maximum number of exclamations */
    text-align: left; /* Ensures the text is aligned within the fixed width */
  }

  .exclaimation-textShadow{
    text-shadow: 2px 2px 6px var(--text-color); /* Add a more prominent shadow */
  }

